___CSS_LOADER_EXPORT___.push([module.id, `* {
  font-family: "Philosopher", sans-serif;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: geometricPrecision;
  outline: 0;
  -webkit-tap-highlight-color: transparent;
}

body,
html {
  background-color: rgb(244, 244, 244);
  height: 100%;
  overflow: hidden;
}

h1 {
  font-size: 2.5em;
}
h1 span {
  color: green;
}

.section-quote {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrapper {
  background-color: #fee6e3;
  border: 2px solid #000;
  border-radius: 8px;
  box-sizing: border-box;
  color: #000;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
}
.wrapper undefined:after {
  background-color: #000;
  border-radius: 8px;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  transform: translate(8px, 8px);
  transition: transform 0.2s ease-out;
  z-index: -1;
}

.keyboard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.quote-letters-count {
  margin-bottom: 20px;
}

.badge {
  padding: 4px 8px;
  text-align: center;
  border-radius: 5px;
  margin-left: 4px;
  color: #fff;
  background-color: #000;
}

.info-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.start-btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer {
  margin-top: 20px;
}

button {
  background-color: #000;
  border-radius: 4px;
  border-style: none;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  max-width: none;
  min-height: 44px;
  min-width: 10px;
  margin-bottom: 20px;
  outline: none;
  overflow: hidden;
  padding: 9px 20px 8px;
  position: relative;
  text-align: center;
  text-transform: none;
  user-select: none;
}
button undefined:hover,
button undefined:focus {
  opacity: 0.75;
}

.opacity-on {
  opacity: 1;
  transition: opacity 1s ease-out;
}

.opacity-off {
  opacity: 0;
}`, "",{"version":3,"sources":["webpack://./src/components/index.scss"],"names":[],"mappings":"AAAA;EACE,sCAAA;EACA,sBAAA;EACA,mCAAA;EACA,kCAAA;EACA,kCAAA;EACA,UAAA;EACA,wCAAA;AACF;;AAEA;;EAEE,oCAAA;EACA,YAAA;EACE,gBAAA;AACJ;;AAEA;EACE,gBAAA;AACF;AAAE;EACE,YAAA;AAEJ;;AAEA;EACE,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;AACF;;AAEA;EACE,yBAAA;EACA,sBAAA;EACA,kBAAA;EACA,sBAAA;EACA,WAAA;EACA,eAAA;EACA,eAAA;EACA,kBAAA;AACF;AACE;EACE,sBAAA;EACA,kBAAA;EACA,WAAA;EACA,cAAA;EACA,YAAA;EACA,OAAA;EACA,WAAA;EACA,kBAAA;EACA,SAAA;EACA,8BAAA;EACA,mCAAA;EACA,WAAA;AACJ;;AAGA;EACE,aAAA;EACA,uBAAA;EACA,mBAAA;AAAF;;AAGA;EACE,mBAAA;AAAF;;AAGA;EACE,gBAAA;EACA,kBAAA;EACA,kBAAA;EACA,gBAAA;EACA,WAAA;EACA,sBAAA;AAAF;;AAGA;EACE,aAAA;EACA,8BAAA;EACA,mBAAA;AAAF;;AAGA;EACE,aAAA;EACA,uBAAA;EACA,mBAAA;AAAF;;AAGA;EACE,gBAAA;AAAF;;AAGA;EACE,sBAAA;EACA,kBAAA;EACA,kBAAA;EACA,WAAA;EACA,eAAA;EACA,eAAA;EACA,gBAAA;EACA,eAAA;EACA,gBAAA;EACA,eAAA;EACA,mBAAA;EACA,aAAA;EACA,gBAAA;EACA,qBAAA;EACA,kBAAA;EACA,kBAAA;EACA,oBAAA;EACA,iBAAA;AAAF;AACE;;EAEE,aAAA;AACJ;;AAGA;EACE,UAAA;EACA,+BAAA;AAAF;;AAGA;EACE,UAAA;AAAF","sourcesContent":["* {\n  font-family: \"Philosopher\", sans-serif;\n  box-sizing: border-box;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  text-rendering: geometricPrecision;\n  outline: 0;\n  -webkit-tap-highlight-color: transparent;\n}\n\nbody,\nhtml {\n  background-color: rgb(244, 244, 244);\n  height: 100%;\n    overflow: hidden;\n}\n\nh1 {\n  font-size: 2.5em;\n  span {\n    color: green;\n  }\n}\n\n.section-quote {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.wrapper {\n  background-color: #fee6e3;\n  border: 2px solid #000;\n  border-radius: 8px;\n  box-sizing: border-box;\n  color: #000;\n  max-width: 100%;\n  padding: 0 25px;\n  position: relative;\n\n  undefined:after {\n    background-color: #000;\n    border-radius: 8px;\n    content: \"\";\n    display: block;\n    height: 100%;\n    left: 0;\n    width: 100%;\